import React from 'react'

import Layout from '../components/layout/layout'
import PageContent from '../components/layout/pageContent';
import Agenda from '../components/content/agendaComponent';
import items_en from '../data/api/data--agenda_component--en';
import items_fr from '../data/api/data--agenda_component--fr';

const AgendaPage = ({pageContext}) => {
    let items = pageContext.lang === 'en' ? items_en : items_fr;
    return (
        <Layout context={pageContext}>
            <PageContent context={pageContext}/>
            <Agenda lang={pageContext.lang} items={items}/>
        </Layout>
    )
};

export default AgendaPage
