import React from 'react';
import PropTypes from 'prop-types';
import YearCarousel from '../layout/yearCarousel';
import ArrayHandler from '../../utils/array-handler';
import '../../styles/animate.min.css';
import _ from 'lodash';
import {Container, Row, Col} from 'reactstrap';
import Time from '../layout/time';

export default class Agenda extends React.Component {
    constructor() {
        super();
        this.state = {
            currentYear: 0
        }
    }

    static propTypes = {
        items: PropTypes.object.isRequired,
        lang: PropTypes.string.isRequired
    };

    selectYear = (year) => {
        this.setState({
            currentYear: year
        });
    };

    render() {
        let years = _.keys(this.props.items).sort().reverse();
        return (
            <Container>
                <Row>
                    <Col xs="12">
                        <YearCarousel years={years} clickCallback={this.selectYear}/>
                    </Col>
                </Row>
                <Row>
                    <Col lg={{ size: 6, offset: 3 }}  md={{ size: 8, offset: 2 }} xs="12" className="financial-publication">
                        {years.map(year => (
                            <div key={year}  className="financial-publication-container">
                                {this.state.currentYear === year || this.state.currentYear === 0 ? (
                                    <div className={'animated fast bounceInUp'}>
                                        <h4>{year}</h4>
                                        <div>
                                            {ArrayHandler.sortColumns(this.props.items[year], ['date', 'title'], ['desc', 'desc']).map(node => (
                                                <Row key={node.nid} className="financial-publication-item">
                                                    <Time lang={this.props.lang} date={node.date}/>
                                                    <Col lg="9"  md="8" xs="8">
                                                        <h5>{node.title}</h5>
                                                    </Col>
                                                </Row>
                                            ))}
                                        </div>
                                    </div>
                                ) : null}
                            </div>
                        ))}
                    </Col>
                </Row>
            </Container>
        )
    }
}
